<template>
  <div>
    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut"  mode='out-in'>
      <keep-alive>
        <router-view v-cloak v-if="$route.meta.keepAlive" class="active_view"></router-view>
      </keep-alive>
    </transition>
    <transition  enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode='out-in'>
      <router-view v-cloak v-if="!$route.meta.keepAlive" class="active_view"></router-view>
    </transition>
    <Foot_Nav v-if="Isshow"></Foot_Nav>
  </div>

</template>

<script>
import Foot_Nav from "./components/foot/foot.vue";
export default {
  data() {
    return {
      transitionName: "",
      is_Adclass: true,
      Isshow: true
    };
  },
  components: {
    Foot_Nav
  },
  mounted() {
    if (this.$route.name == "details") {
      this.Isshow = false
    }
  },
  watch: {
    $route(to, from) {

      if (to.name == "details" || to.name == "login" || to.name == "reg") {
        this.Isshow = false;
        
      } else {
        this.Isshow = true;
      }
      // title
      if (to.meta.title) {
        document.title = to.meta.title;
      }
    }
  }
};
</script>

<style>
[v-cloak] {
  display: none;
}


.active_view {
  box-sizing: border-box;
  bottom: 0;
  height: 100%;
  width: 100%;
  max-width: 780px;
  transition: all .3s cubic-bezier(0.55, 0, 0.1, 1);
}
</style>


